import React from 'react';
import { push } from 'react-router-redux'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import {Link} from 'react-router-dom';
import './index.css';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Button,
  Form } from 'reactstrap';
  import {
    login,
    loginWithAPI,
    logout
  } from '../../modules/user'
class Header extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleSideNav = this.toggleSideNav.bind(this);
    this.state = {
      isOpen: false,
      sideNav:false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    },
  ()=>{
    console.log(this.state);
    this.forceUpdate();
  });
  }
  getLink(text,link){
    return  (
      <Link className="dropdown-item" to={link}>{text}</Link>
    )
  }
  getNav(detail){
    return (
      <Link className="navbar-brand" to="/">{detail=="l"?"LessForDress":"L4D"}</Link>
    )
  }
  toggleSideNav(){
    this.setState({
      sideNav:!this.state.sideNav
    })

  }
  render() {
    return (
      <div className={this.state.sideNav?"sidenav-overlay":''}>

<div id="mySidenav" style={{width:this.state.sideNav?"200px":"0"}} className="sidenav">
  <a href="javascript:void(0)" className="closebtn" onClick={this.toggleSideNav}>&times;</a>
  {this.getLink("Add Event","/event")}
  <a href="#">Add Item</a>
  <a href="#">Homesale</a>
</div>

      <div className="d-none d-sm-block">
        <Navbar dark={true} color="dark" expand="xs">
        {this.props.loggedIn && (<Button className="mr-2" style={{background:"transparent",outline:"none",border:"none"}}
        onClick={this.toggleSideNav}><i className="fa fa-bars"></i></Button>)}
          <NavbarBrand tag={()=>this.getNav("l")}></NavbarBrand>
          <Nav className=" ml-auto" navbar>
          <Form className="form-inline mr-auto ml-auto">
                    <input className="form-control mr-sm-2" type="text" placeholder="Search" aria-label="Search"/>

                    <button className="btn btn-default" type="submit"><i className="fa fa-search"></i></button>
                </Form>

          </Nav>
              {this.props.loggedIn && (
                <Nav className=" ml-auto" navbar>
                <NavItem>
                <Link className="nav-link" to="/about-us">About</Link>
              </NavItem>
              <NavItem>
                <Link className="nav-link" to="/account">My Dashboard</Link>
              </NavItem>
              <NavItem>
                <Link className="nav-link" to="/list">My Favourites</Link>
              </NavItem>

              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Options
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem tag={()=>this.getLink("Edit Details","/profile")}>

                  </DropdownItem>
                  <DropdownItem tag={()=>this.getLink("My Profile","/information")}>

                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem tag={()=>(<button className="dropdown-item"  onClick={this.props.logout}>Logout</button>)}>

                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>)}
              {!this.props.loggedIn && (
                  <Nav className=" ml-auto" navbar>
                  <NavItem>
                <Link className="nav-link" to="/login">Login</Link>
              </NavItem>
            </Nav>)}

        </Navbar>
      </div>
      <div className="d-block d-sm-none">
        <Navbar dark={true} color="dark" expand="xs">

        <div id="mySidenav" style={{width:this.state.sideNav?"200px":"0"}} className="sidenav">
          <a href="javascript:void(0)" className="closebtn" onClick={this.toggleSideNav}>&times;</a>
          {this.getLink("Add Event","/event")}
          <a href="#">Add Item</a>
          <a href="#">Homesale</a>
        </div>
        {this.props.loggedIn && (<Button className="" style={{background:"transparent",outline:"none",border:"none"}}
        onClick={this.toggleSideNav}><i className="fa fa-bars"></i></Button>)}
          <NavbarBrand tag={()=>this.getNav("m")}></NavbarBrand>
          <Nav className="mr-auto ml-auto" navbar>
          <Form className="ml-auto" inline row>
                    <input style={{"max-width":"90px"}} className="form-control" type="text" placeholder="Search" aria-label="Search"/>
                </Form>

          </Nav>
              {this.props.loggedIn && (
                  <Nav className=" ml-auto" navbar>
                <NavItem>
                <Link className="nav-link" to="/account"><i className="fa fa-window-maximize"></i></Link>
              </NavItem>
              <NavItem>
                <Link className="nav-link" to="/list"><i className="fa fa-heart"></i></Link>
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  <i className="fa fa-user-o"></i>
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem tag={()=>this.getLink("Edit Details","/profile")}>

                  </DropdownItem>
                  <DropdownItem tag={()=>this.getLink("My Profile","/information")}>

                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem tag={()=>(<button className="dropdown-item" onClick={this.props.logout}>Logout</button>)}>
                    Reset
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>)}
              {!this.props.loggedIn && (
                <Nav className=" ml-auto" navbar>
                <NavItem>
              <Link className="nav-link" to="/login">Login</Link>
            </NavItem>
          </Nav>)}
        </Navbar>
      </div>

      </div>
    );
  }
}
const mapStateToProps = state => ({
  loggedIn: state.user.loggedIn,
  loggingIn: state.user.loggingIn,
})
const mapDispatchToProps = dispatch => bindActionCreators({
  login,
  loginWithAPI,
  logout
}, dispatch)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Header)



