import React, { Component } from 'react';
import { TabContent, TabPane, Button, Collapse, Nav, NavItem, NavLink, Navbar, NavbarToggler, NavbarBrand,
    Row, Col, Modal, ModalBody, Form, FormGroup, Label, Input } from 'reactstrap'
import './index.css';
import classnames from 'classnames';
import SuccessOutlineButton from '../button/Button';
import { push } from 'react-router-redux'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { NetworkApi } from '../../NetworkApi';
import store from '../../store';
import FacebookLogin from 'react-facebook-login';
import {
  login,
  loginWithAPI,
} from '../../modules/user'
class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeTab: '1'
        };
        this.toggleTab = this.toggleTab.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.signUp = this.signUp.bind(this);
        this.responseFacebook=this.responseFacebook.bind(this);
        this.componentClicked=this.componentClicked.bind(this);
        console.log(this.props);
    }
    componentWillReceiveProps(nextProps){
    }
    toggleTab(tab) {
        if (this.state.activeTab !== tab) {
            this.setState({
                activeTab: tab
            });
        }
    }
    responseFacebook(response){
      console.log(response);
    }
    componentClicked(e){
      console.log(e);
    }
    handleInputChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }
    signUp(){
      console.log(this.state);
      store.dispatch({
        type:"global/START_LOADING"
      });
      NetworkApi.register(this.state.name,this.state.sPassword,this.state.scPassword).then(function(res){
        store.dispatch({
          type:"global/STOP_LOADING"
        });
        store.dispatch({
          type:"user/LOGIN"
        });
      }).catch(function(err){
        store.dispatch({
          type:"global/STOP_LOADING"
        });
        store.dispatch({
          type:"user/LOGIN"
        });
      });
    }
    render() {
      console.log(this)
        return (
          <div>
                           <Nav tabs justified>
                               <NavItem className="tabHeading">
                                   <NavLink
                                       className={classnames({ active: this.state.activeTab === '1' })}
                                       onClick={() => { this.toggleTab('1'); }}
                                   >
                                       Existing User
                               </NavLink>
                               </NavItem>
                               <NavItem className="tabHeading">
                                   <NavLink
                                       className={classnames({ active: this.state.activeTab === '2' })}
                                       onClick={() => { this.toggleTab('2'); }}
                                   >
                                       Sign Up
                               </NavLink>
                               </NavItem>
                           </Nav>
                           <TabContent activeTab={this.state.activeTab}>
                           <TabPane tabId="1">
                               <Row>
                                   <Col sm="10" className="mt-4 offset-sm-1">
                                       <h6 className="text-uppercase text-center pb-2">Sign in with email</h6>
                                       <Form>
                                           <FormGroup>
                                               <Label for="lemail" className="text-uppercase">Email Address</Label>
                                               <Input type="email" name="email" id="lemail" placeholder="email placeholder" />
                                           </FormGroup>
                                           <FormGroup>
                                               <Label for="lPassword" className="text-uppercase">Password</Label>
                                               <Input type="password" name="password" id="lPassword" placeholder="password placeholder" />
                                           </FormGroup>
                                       </Form>
                                       <div className="text-center mt-4 mb-3">
                                           <Button className="button btn btn-secondary" onClick={this.props.loginWithAPI}>Sign In</Button>
                                       </div>
                                   </Col>
                               </Row>
                               <hr className="w-75 mx-auto" />
                               <p className="text-uppercase text-center mt-3">Sign In with...</p>
                               <div className="text-center mt-3 mb-4">
                               <FacebookLogin
                                  appId="2044210959231662"
                                  autoLoad={true}
                                  fields="name,email,picture"
                                  cssClass="button btn btn-secondary"
                                  icon="fa-facebook-square"
                                  textButton=" FACEBOOK"
                                  onClick={this.componentClicked}
                                  callback={this.responseFacebook} />
                                  </div>

                           </TabPane>
                               <TabPane tabId="2">
                                   <Row>
                                       <Col sm="10" className="mt-4 offset-sm-1">
                                           <h6 className="text-uppercase text-center pb-2">Sign up with email</h6>
                                           <Form>
                                               <FormGroup>
                                                   <Label for="sName" className="text-uppercase">Name</Label>
                                                   <Input type="text" name="name" id="sName" onChange={this.handleInputChange} placeholder="Enter name" />
                                               </FormGroup>
                                               <FormGroup>
                                                   <Label for="sEmail" className="text-uppercase">Email Address</Label>
                                                   <Input type="email" name="email" id="sEmail" onChange={this.handleInputChange} placeholder="Enter email" />
                                               </FormGroup>
                                               <FormGroup>
                                                   <Label for="sPassword" className="text-uppercase">Password</Label>
                                                   <Input  type="password" name="password" id="sPassword" onChange={this.handleInputChange} placeholder="Enter password" />
                                               </FormGroup>
                                               <FormGroup>
                                                   <Label for="scPassword" className="text-uppercase">Password</Label>
                                                   <Input  type="password" name="cpassword" id="scPassword" onChange={this.handleInputChange} placeholder="Confirm password" />
                                               </FormGroup>
                                               <div className="text-center mt-4 mb-3">
                                                   <Button onClick={this.signUp} className="btn button">Sign Up</Button>
                                               </div>
                                           </Form>
                                       </Col>
                                   </Row>
                                   <hr className="w-75 mx-auto" />
                                   <p className="text-uppercase text-center mt-3">Sign Up with...</p>
                                   <div className="text-center mt-3 mb-4">
                                       <SuccessOutlineButton onClick={this.toggle}><a href={'/'} className="text-uppercase nav-link"><i className="fa fa-facebook-square mr-2"></i>Facebook</a></SuccessOutlineButton>
                                   </div>
                               </TabPane>

                           </TabContent>
                   </div >
        );
    }
  }
  const mapStateToProps = state => ({
    login: state.user.loggedIn,
    loggingIn: state.user.loggingIn
  })
  const mapDispatchToProps = dispatch => bindActionCreators({
    login,
    loginWithAPI,
  }, dispatch)

  export default connect(
    mapStateToProps,
    mapDispatchToProps
  )(Login)



