function deleteValue(id){
    return axios.delete(`${SERVER_API}${format(Routes.DELETE_VALUE, {id: id})}`)
}

export const NetworkApi = {
    getItemNames,
    getItems,
    getItemDetailsById,
    postItem,
    deleteItemById,
    getUserInfo,
    logout,
    getManageInfo,
    changePassword,
    setPassword,
    addExternalLogin,
    removeExternalLogin,
    externalLogin,
    externalLogins,
    register,
    registerExternal,
    getValues,
    getValueById,
    deleteValue
}



