import { push } from 'react-router-redux';
import store from '../store';
import { NetworkApi } from '../NetworkApi';
export const LOGIN = "user/LOGIN";
export const LOGOUT = "user/LOGOUT";
export const LOGIN_REQUESTED = "user/LOGIN_REQUESTED";
export const USER_SIGNUP = "user/USER_SIGNUP";
export const USER_EDIT = "user/USER_EDIT";
export const SET_USER = "user/SET_USER";
export const REGISTER_EXTERNAL = "user/REGISTER_EXTERNAL";


const initialState = {
  loggedIn: true,
  loggingIn:false
}
export default (state = initialState, action) => {
  console.log(state);
  switch (action.type) {
    case LOGIN_REQUESTED:
      return {
        ...state,
        loggingIn: true
      }
    case SET_USER:
      return {
        ...state,
        user:action.payload.username,
        password:action.payload.password
      }
    case LOGIN:
      return {
        ...state,
        loggedIn: true,
        loggingIn: !state.loggingIn,
        password:null
      }
      case USER_SIGNUP:
        return {
          ...state,
          loggedIn: true,
          loggingIn: !state.loggingIn,
          password:null
        }
      case LOGOUT:
        return {
          ...state,
          loggedIn: false,
          loggingIn: !state.loggingIn,
          password:null
        }
    default:
      return state
  }
}


export const login = (data) => {

  return dispatch => {
    dispatch({
      type: SET_USER,
      payload: data
    })

    dispatch({
      type: LOGIN_REQUESTED
    })
    dispatch({
      type: LOGIN
    })
  }
}

export const loginWithAPI = () => {
  return dispatch => {
    dispatch({
      type: LOGIN_REQUESTED
    })
    store.dispatch({
      type:"global/START_LOADING"
    })
    return setTimeout(() => {

      dispatch({
        type: LOGIN,
      });
      store.dispatch({
        type:"global/STOP_LOADING"
      })
      dispatch(push('/'));
    }, 3000)
  }
}

export const logout = () => {
  console.log("logged out");
  return dispatch => {
    dispatch({
      type: LOGOUT
    })
  }
}

export const signUp = () => {
  console.log("logged out");
  return dispatch => {
    dispatch({
      type: USER_SIGNUP
    })
  }
}

export const signUpExternal = () => {
  console.log("logged out");
  return dispatch => {
    dispatch({
      type: REGISTER_EXTERNAL
    })
  }
}



