
export const LOAD_PRODUCTS = "product/LOAD_PRODUCTS";


const initialState = {
  products:[]
}
export default (state = initialState, action) => {
  console.log(state);
  switch (action.type) {
      case LOAD_PRODUCTS:
        return {
          ...state,
          products:action.payload
        }

    default:
      return state
  }
}


export const loadProducts = (data) =>{
  //console.log(data);
  return dispatch => {
    dispatch({
      type:LOAD_PRODUCTS,
      payload:data
    })
  };
}



